import React from 'react'
import css from './MyAvatar.module.css'

function MyAvatar({ userProfile }) {
	return (
		<div className={css.profile__avatar}>
			<div className={css.avatar}>
				<img src={userProfile.photos.large} alt="Фото профиля"/>
			</div>
			<div className={css.avatar__description}>
				<div className={css.description__item}>{userProfile.fullName}</div>
				<div className={css.description__item}>{userProfile.aboutMe}</div>
				<div className={css.description__item}>{userProfile.lookingForAJob}</div>
				<div className={css.description__item}>{userProfile.lookingForAJobDescription}</div>
			</div>
		</div>
	)
}

export default MyAvatar
