import React from 'react'
import css from './Profile.module.css'

import MyPostsContainer from './MyPosts/MyPostsContainer'
import MyAvatar from './MyAvatar/MyAvatar'
import MyBackground from './MyBackground/MyBackground'
import Preloader from '../../components/Preloader/Preloader'
import MyStatusHook from './MyStatus/MyStatusHook'

const Profile = ({ userProfile, status, updateStatus }) => {
	return (
		<>
			{ !userProfile 
				? <Preloader/> 
				: 	<div className={css.profile}>
						<MyBackground />
						<div className={css.profile__body}>
							<MyAvatar userProfile={userProfile}/>
							<MyStatusHook status={status} updateStatus={updateStatus} />
							<MyPostsContainer />
						</div>
					</div>
			}
		</>
	)
}

export default Profile
